.footer {
  position: sticky;
  bottom: 0;
  z-index: -1;
  height: 540px;
  background-image: url(../images/footer.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &__logo {
    position: absolute;
    left: 50%;
    bottom: 95px;
    transform: translate(-50%);
  }
}
